import {
  signInWithEmailAndPassword,
  signUpWithEmailAndPassword,
  signOut as logOut,
  getUserData,
  updateUserData as updateUser
} from "../services/user";

export const LOGIN_SUCCESS = "LOGIN_SUCCESS";
export const LOGIN_ERROR = "LOGIN_ERROR";
export const SIGN_UP_SUCCESS = "SIGN_UP_SUCCESS";
export const SIGN_UP_ERROR = "SIGN_UP_ERROR";
export const SIGN_OUT_SUCCESS = "SIGN_OUT_SUCCESS";
export const GET_CURRENT_USER_SUCCESS = "GET_CURRENT_USER_SUCCESS";
export const TOGGLE_SIGN_IN_MODAL = "TOGGLE_SIGN_IN_MODAL";
export const TOGGLE_SIGN_UP_MODAL = "TOGGLE_SIGN_UP_MODAL";
export const TOGGLE_LOADING = "TOGGLE_LOADING";
export const UPDATE_USER_SUCCESS = "UPDATE_USER_SUCCESS";
export const UPDATE_USER_ERROR = "UPDATE_USER_ERROR";

export const toggleLoading = () => {
  return dispatch => {
    dispatch({
      type: TOGGLE_LOADING
    });
  };
};
export const toggleSignInModal = () => {
  return dispatch => {
    dispatch({
      type: TOGGLE_SIGN_IN_MODAL
    });
  };
};
export const toggleSignUpModal = () => {
  return dispatch => {
    dispatch({
      type: TOGGLE_SIGN_UP_MODAL
    });
  };
};

export const updateUserData = data => {
  return dispatch => {
    updateUser(data)
      .then(data => {
        dispatch({
          type: UPDATE_USER_SUCCESS,
          payload: data.data
        });
      })
      .catch(error => {
        dispatch({
          type: UPDATE_USER_ERROR,
          payload: error
        });
      });
  };
};

export const getCurrentUser = () => {
  const token = localStorage.getItem("token");
  return dispatch => {
    getUserData(token)
      .then(data => {
        dispatch({
          type: GET_CURRENT_USER_SUCCESS,
          payload: data.data
        });
      })
      .catch(error => {
        console.log(error);
      });
  };
};
export const signIn = (email, password) => {
  if (email === "" || password === "") {
    return dispatch => {
      dispatch({
        type: LOGIN_ERROR,
        error: "Заполните поля"
      });
    };
  } else {
    return dispatch => {
      signInWithEmailAndPassword(email, password)
        .then(data => {
          const token = data.data.auth_token;
          localStorage.setItem("token", token);
          dispatch({
            type: LOGIN_SUCCESS,
            payload: data.data
          });
        })
        .catch(error => {
          console.log(error);

          dispatch({
            type: LOGIN_ERROR,
            // error
            error: error
          });
        });
    };
  }
};
export const signUp = (
  email,
  phone,
  gender,
  first_name,
  last_name,
  birth_day,
  password
) => {
  return dispatch => {
    signUpWithEmailAndPassword(
      email,
      phone,
      gender,
      first_name,
      last_name,
      birth_day,
      password
    )
      .then(data => {
        const token = data.data.token;
        localStorage.setItem("token", token);

        dispatch({
          type: SIGN_UP_SUCCESS,
          payload: data.data
        });
      })
      .catch(error => {
        dispatch({
          type: SIGN_UP_ERROR,
          error: error.response.data.error_message
        });
      });
  };
};
export const signOut = () => {
  return dispatch => {
    logOut().then(() => {
      dispatch({
        type: SIGN_OUT_SUCCESS
      });
    });
  };
};
