import { GET_HOUSE_SUCCESS, GET_HOUSE_ERROR } from "../actions/house.action";

const initState = {};

export default function(state = initState, action) {
  switch (action.type) {
    case GET_HOUSE_SUCCESS:
      return { ...action.payload };
    case GET_HOUSE_ERROR:
      return {
        state
      };

    default:
      return state;
  }
}
