import React, { Component } from "react";

import { getFavourites, toggleFavourite } from "../../services/favourites";

import ReactStars from "react-rating-stars-component";
import { FontAwesomeIcon as Fa } from "@fortawesome/react-fontawesome";

import { faHeart, faMapMarkerAlt } from "@fortawesome/free-solid-svg-icons";
import { faHeart as faHeartReg } from "@fortawesome/free-regular-svg-icons";
import "./Saved.scss";
import Container from "react-bootstrap/Container";

class Saved extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    getFavourites()
      .then(data => {
        this.setState({
          favourites: data.data
        });
      })
      .catch(e => {
        console.log(e);
      });
  }
  toggleFavs = (event, liked, id) => {
    event.stopPropagation();
    toggleFavourite(liked, id)
      .then(d => {
        getFavourites()
          .then(data => {
            this.setState({
              favourites: data.data
            });
          })
          .catch(e => {
            console.log(e);
          });
      })
      .catch(e => {
        console.log(e);
      });
  };
  render() {
    const { favourites } = this.state;
    return (
      <Container>
        <div className="Saved">
          <h2 className="Saved__title">Сохранённые</h2>

          {favourites && favourites.length > 0 ? (
            <div className="Results__list">
              {favourites.map((result, index) => (
                <div
                  className="Results__list--item"
                  onClick={() => this.goToResult(result.house.id)}
                  key={index}
                >
                  <div className="Results__list--item-top">
                    <div
                      onClick={e => {
                        this.toggleFavs(
                          e,
                          result.house.is_favourite,
                          result.house.id
                        );
                      }}
                      className={`Results__list--like ${
                        result.house.is_favourite === true
                          ? "Results__list--liked"
                          : ""
                      }`}
                    >
                      {result.house.is_favourite === true ? (
                        <Fa icon={faHeart} />
                      ) : (
                        <Fa icon={faHeartReg} />
                      )}
                    </div>

                    <img
                      className="Results__list--image"
                      src="http://static.trip101.com/paragraph_media/pictures/000/216/877/large/1024px-TV-Turm_Almaty_-_3.jpg?1500450764"
                      alt=""
                    />
                    <p className="Results__list--price">
                      {result.house.price} тг/ночь
                    </p>
                  </div>
                  <div className="Results__list--item-bottom">
                    <p className="Results__list--type">
                      {result.house.house_type}
                    </p>
                    <p className="Results__list--name">{result.house.name}</p>
                    <p className="Results__list--details">
                      {result.house.guests} Гостя, {result.house.rooms}{" "}
                      комната(-ы), {result.house.beds} кровати{" "}
                    </p>
                    <h2 className="Results__list--place">
                      <Fa icon={faMapMarkerAlt} />
                      {result.house.city}
                    </h2>
                    <button className="Results__list--button">Смотреть</button>
                    <ReactStars
                      edit={false}
                      count={5}
                      value={result.house.rating}
                      onChange={this.ratingChanged}
                      size={22}
                      color1={"#bdbcbc"}
                      color2={"#CD3232"}
                    />
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div className="Saved__empty">
              <div className="Saved__like">
                <Fa icon={faHeart} />
              </div>
              <p className="Saved__paragraph">
                Сохраняйте понравившиеся вам жилья
                <br /> в одном месте, нажав на
                <br /> сердечку на любом жилье.
              </p>
              <button className="Saved__button">Найти жильё</button>
            </div>
          )}
        </div>
      </Container>
    );
  }
}

export default Saved;
