import React, { Component, Fragment } from "react";

import { connect } from "react-redux";
import { withRouter } from "react-router-dom";

import { YMaps, Map, Placemark } from "react-yandex-maps";
import { getHouses } from "../../actions/house.action";
import { toggleFavourite } from "../../services/favourites";
import "./Results.scss";
import ReactStars from "react-rating-stars-component";
import Collapse from "react-bootstrap/Collapse";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { FontAwesomeIcon as Fa } from "@fortawesome/react-fontawesome";
import ReactSlider from "react-slider";
import {
  faHeart,
  faMapMarkerAlt,
  faAngleDown,
  faAngleUp
} from "@fortawesome/free-solid-svg-icons";
import { faHeart as faHeartReg } from "@fortawesome/free-regular-svg-icons";
import { Options, TypeOptions } from "../UIComponents/Select";

import { START_DATE, END_DATE } from "@datepicker-react/styled";
import styled from "styled-components";
import { DatePicker } from "../UIComponents/DatePicker";
import Container from "react-bootstrap/Container";

const mapData = {
  center: [43.238949, 76.889709],
  zoom: 12
};

const StyledTrack = styled.div`
  top: 6px;
  bottom: 0;
  height: 12px;
  background: ${props =>
    props.index === 2 ? "#E5E5E5" : props.index === 1 ? "#cd3232" : "#E5E5E5"};
  border-radius: 999px;
`;

const Track = (props, state) => <StyledTrack {...props} index={state.index} />;

class Results extends Component {
  constructor(props) {
    super(props);
    this.state = {
      city__id: null,
      city_name: "",
      start_date: "",
      end_date: "",

      focusedInput: null,
      mapVisibility: true,

      certifiedHouse: false,
      house_type__name: null,
      type: {
        value: 0,
        label: "Любой"
      },
      sort: "none",
      price__gte: 100,
      price__lte: 20000,
      rooms__gte: 1,
      rooms__lte: 10,
      beds__gte: 1,
      beds__lte: 20,

      filterOpen: false
    };
  }
  componentWillMount() {
    const paramsUrl = new URLSearchParams(this.props.location.search);
    const params = {};
    for (var pair of paramsUrl.entries()) {
      params[pair[0]] = pair[1];
    }
    console.log(params);
    params["start_date"] = new Date(params["start_date"]);
    params["end_date"] = new Date(params["end_date"]);
    const { getHouses } = this.props;
    getHouses(params);
    this.setState({
      ...params
    });
  }
  mapVisibilityChangeHandler = () => {
    this.setState({
      mapVisibility: !this.state.mapVisibility
    });
  };
  goToResult = id => {
    this.props.history.push("/housing/" + id);
  };
  dateFocusHandler = e => {
    this.setState({
      focusedInput: e.target.name === "from" ? START_DATE : END_DATE
    });
  };
  convert = str => {
    var date = new Date(str),
      mnth = ("0" + (date.getMonth() + 1)).slice(-2),
      day = ("0" + date.getDate()).slice(-2);
    return [date.getFullYear(), mnth, day].join("-");
  };
  priceSliderChangeHandler = e => {
    this.setState({
      price__gte: e[0],
      price__lte: e[1]
    });
  };
  roomsSliderChangeHandler = e => {
    this.setState({
      rooms__gte: e[0],
      rooms__lte: e[1]
    });
  };
  bedsSliderChangeHandler = e => {
    this.setState({
      beds__gte: e[0],
      beds__lte: e[1]
    });
  };
  toggleFilter = () => {
    this.setState({
      filterOpen: !this.state.filterOpen
    });
  };
  toggleSortOption = e => {
    this.setState({
      sort: e.target.name
    });
  };
  citySelectChangeHandler = e => {
    this.setState({
      city__id: e.value,
      city_name: e.label
    });
  };
  typeChangeHanlder = e => {
    this.setState({
      type: e
    });
  };
  resetFilter = e => {
    e.preventDefault();
    this.setState({
      certifiedHouse: false,
      type: {
        value: 0,
        label: "Любой"
      },
      sort: "none",
      priceSlider: [1000, 20000],
      roomsSlider: [1, 10],
      bedsSlider: [1, 20]
    });
  };
  applyFilter = e => {
    let params = {
      city__id: this.state.city__id,
      city_name: this.state.city_name,
      start_date: this.state.start_date,
      end_date: this.state.end_date
    };
    params["price__gte"] = this.state.price__gte;
    params["price__lte"] = this.state.price__lte;

    params["beds__gte"] = this.state.beds__gte;
    params["beds__lte"] = this.state.beds__lte;

    params["rooms__gte"] = this.state.rooms__gte;
    params["rooms__lte"] = this.state.rooms__lte;

    if (this.state.type.value !== 0)
      params["house_type__name"] = this.state.type.label;

    const paramString = new URLSearchParams(params);
    this.props.history.push({
      pathname: "/results",
      search: `?${paramString.toString()}`
    });

    this.setState({
      filterOpen: false
    });
    window.location.reload();
  };
  queryStringParse = function(string) {
    let parsed = {};
    if (string !== "") {
      string = string.substring(string.indexOf("?") + 1);
      let p1 = string.split("&");
      p1.array.forEach(element => {
        let params = element.split("=");
        parsed[params[0]] = params[1];
      });
      // p1.map(function(value) {
      //   let params = value.split("=");
      //   parsed[params[0]] = params[1];
      // });
    }
    return parsed;
  };
  toggleFavs = (event, liked, id) => {
    event.stopPropagation();
    console.log(liked, id);
    toggleFavourite(liked, id).then(d => {
      window.location.reload();
    });
    // this.applyFilter(event);
  };

  render() {
    const { houses, reference } = this.props;
    return (
      <Fragment>
        <div className="Results__filter">
          <label
            className={`Results__filter--label ${
              this.state.mapVisibility ? "Results__filter--label-checked" : null
            }`}
            onClick={this.mapVisibilityChangeHandler}
            htmlFor="mapVisibility"
          >
            Показать на карте
          </label>
          <Options
            marginRight={10}
            placeholder="Куда угодно"
            onChange={this.citySelectChangeHandler}
            defaultValue={{
              value: this.state.city__id,
              label: this.state.city_name
            }}
            options={
              reference.cities &&
              reference.cities.map(city => {
                return {
                  value: city.id,
                  label: city.name
                };
              })
            }
          />
          <input
            name="from"
            onFocus={this.dateFocusHandler}
            className="Results__filter--input"
            type="date"
            placeholder="дд/мм/гггг"
            value={this.convert(this.state.start_date)}
          />
          <input
            name="to"
            value={this.convert(this.state.end_date)}
            className="Results__filter--input"
            type="date"
            onFocus={this.dateFocusHandler}
            placeholder="дд/мм/гггг"
          />
          {this.state.focusedInput !== null ? (
            <div className="Results--datepicker">
              <DatePicker
                onClose={e => {
                  e.preventDefault();
                  this.setState({
                    focusedInput: null
                  });
                }}
                onDatesChange={data => {
                  console.log(data);
                  this.setState({
                    ...this.state,
                    start_date: data.startDate,
                    end_date: data.endDate,
                    focusedInput: data.focusedInput
                  });
                }}
                startDate={this.state.start_date}
                endDate={this.state.end_date}
                focusedInput={this.state.focusedInput}
              />
            </div>
          ) : null}
          <input
            className="Results__filter--input"
            type="number"
            placeholder="Гости"
            style={{ width: "100px" }}
          />
          <button
            onClick={this.applyFilter}
            className="Results__filter--button"
          >
            Найти
          </button>
          <button
            onClick={this.toggleFilter}
            className="Results__filter--filter-button"
            aria-controls="collapse-filter"
            aria-expanded={this.state.filterOpen}
          >
            Фильтры{" "}
            {this.state.filterOpen ? (
              <Fa icon={faAngleUp} />
            ) : (
              <Fa icon={faAngleDown} />
            )}
          </button>
        </div>
        <Collapse in={this.state.filterOpen}>
          <div id="collapse-filter" className="Results__big-filter">
            <Container>
              <p className="Results__big-filter--title">Применить фильтры</p>
              <Row>
                <Col>
                  <label
                    onClick={() => {
                      this.setState({
                        certifiedHouse: !this.state.certifiedHouse
                      });
                    }}
                    className={`Results__big-filter--label Results__filter--label ${
                      this.state.certifiedHouse
                        ? "Results__filter--label-checked"
                        : null
                    }`}
                  >
                    Проверенное жильё
                  </label>
                  <br />
                  <div className="Results__big-filter--select">
                    <TypeOptions
                      onChange={this.typeChangeHanlder}
                      defaultValue={this.state.type}
                      options={[
                        { value: 0, label: "Любой" },
                        { value: 1, label: "Квартира" },
                        { value: 2, label: "Дом" }
                      ]}
                    />
                  </div>
                  <p className="Results__big-filter--label">Тип жилья</p>
                  <div className="Results__big-filter--sort">
                    <button
                      name="none"
                      onClick={this.toggleSortOption}
                      className={`Results__big-filter--sort-option ${
                        this.state.sort === "none"
                          ? "Results__big-filter--sort-selected"
                          : null
                      }`}
                    >
                      нет
                    </button>
                    <button
                      name="raiting"
                      onClick={this.toggleSortOption}
                      className={`Results__big-filter--sort-option  ${
                        this.state.sort === "raiting"
                          ? "Results__big-filter--sort-selected"
                          : null
                      }`}
                    >
                      по рейтингу
                    </button>
                    <button
                      name="price"
                      onClick={this.toggleSortOption}
                      className={`Results__big-filter--sort-option  ${
                        this.state.sort === "price"
                          ? "Results__big-filter--sort-selected"
                          : null
                      }`}
                    >
                      по цене
                    </button>
                  </div>
                  <p className="Results__big-filter--label">Сортировка</p>
                  <button
                    onClick={this.resetFilter}
                    className="Results__big-filter--reset-button"
                  >
                    Сбросить всё
                  </button>
                </Col>
                <Col>
                  <div className="Results__big-filter--range-block">
                    <div className="Results__big-filter--range-block-wrapper">
                      <p className="Results__big-filter--range-label">Цена</p>
                      <p className="Results__big-filter--small">
                        {this.state.price__gte}-{this.state.price__lte} kzt
                      </p>
                    </div>
                    <ReactSlider
                      onChange={this.priceSliderChangeHandler}
                      className="Results__slider"
                      thumbClassName="Results__slider--thumb"
                      renderTrack={Track}
                      value={[this.state.price__gte, this.state.price__lte]}
                      ariaLabel={["Lower thumb", "Upper thumb"]}
                      ariaValuetext={state => `Thumb value ${state.valueNow}`}
                      pearling
                      min={100}
                      max={50000}
                      minDistance={10000}
                    />
                  </div>
                  <div className="Results__big-filter--range-block">
                    <div className="Results__big-filter--range-block-wrapper">
                      <p className="Results__big-filter--range-label">
                        Кол-во комнат
                      </p>
                      <p className="Results__big-filter--small">
                        {this.state.rooms__gte}-{this.state.rooms__lte}
                      </p>
                    </div>
                    <ReactSlider
                      onChange={this.roomsSliderChangeHandler}
                      className="Results__slider"
                      thumbClassName="Results__slider--thumb"
                      renderTrack={Track}
                      value={[this.state.rooms__gte, this.state.rooms__lte]}
                      ariaLabel={["Lower thumb", "Upper thumb"]}
                      ariaValuetext={state => `Thumb value ${state.valueNow}`}
                      pearling
                      min={1}
                      max={20}
                      minDistance={5}
                    />
                  </div>
                  <div className="Results__big-filter--range-block">
                    <div className="Results__big-filter--range-block-wrapper">
                      <p className="Results__big-filter--range-label">
                        Кол-во кроватей
                      </p>
                      <p className="Results__big-filter--small">
                        {this.state.beds__gte}-{this.state.beds__lte}
                      </p>
                    </div>
                    <ReactSlider
                      onChange={this.bedsSliderChangeHandler}
                      className="Results__slider"
                      thumbClassName="Results__slider--thumb"
                      renderTrack={Track}
                      value={[this.state.beds__gte, this.state.beds__lte]}
                      ariaLabel={["Lower thumb", "Upper thumb"]}
                      ariaValuetext={state => `Thumb value ${state.valueNow}`}
                      pearling
                      min={1}
                      max={50}
                      minDistance={10}
                    />
                  </div>

                  <button
                    onClick={this.applyFilter}
                    className="Results__big-filter--button"
                  >
                    Применить
                  </button>
                </Col>
              </Row>
            </Container>
          </div>
        </Collapse>
        <div className="Results">
          {this.state.mapVisibility ? (
            <div className="Results__map">
              <YMaps>
                <Map width="100%" height="100%" defaultState={mapData}>
                  {this.state.results &&
                    this.state.results.map(coordinate => (
                      <Placemark
                        geometry={[coordinate.latitude, coordinate.longitude]}
                      />
                    ))}
                </Map>
              </YMaps>
            </div>
          ) : null}
          <Container>
            <h3 className="Results__list--title">Более 300 вариантов жилья</h3>
            <div className="Results__list">
              {houses &&
                "results" in houses &&
                houses.results.map((result, index) => (
                  <div
                    className="Results__list--item"
                    onClick={() => this.goToResult(result.id)}
                    key={index}
                  >
                    <div className="Results__list--item-top">
                      <div
                        onClick={e => {
                          this.toggleFavs(e, result.is_favourite, result.id);
                        }}
                        className={`Results__list--like ${
                          result.is_favourite === true
                            ? "Results__list--liked"
                            : ""
                        }`}
                      >
                        {result.is_favourite === true ? (
                          <Fa icon={faHeart} />
                        ) : (
                          <Fa icon={faHeartReg} />
                        )}
                      </div>

                      <img
                        className="Results__list--image"
                        src="http://static.trip101.com/paragraph_media/pictures/000/216/877/large/1024px-TV-Turm_Almaty_-_3.jpg?1500450764"
                        alt=""
                      />
                      <p className="Results__list--price">
                        {result.price} тг/ночь
                      </p>
                    </div>
                    <div className="Results__list--item-bottom">
                      <p className="Results__list--type">{result.house_type}</p>
                      <p className="Results__list--name">{result.name}</p>
                      <p className="Results__list--details">
                        {result.guests} Гостя, {result.rooms} комната(-ы),{" "}
                        {result.beds} кровати{" "}
                      </p>
                      <h2 className="Results__list--place">
                        <Fa icon={faMapMarkerAlt} />
                        {result.city}
                      </h2>
                      <button className="Results__list--button">
                        Смотреть
                      </button>
                      <ReactStars
                        edit={false}
                        count={5}
                        value={result.rating}
                        onChange={this.ratingChanged}
                        size={22}
                        color1={"#bdbcbc"}
                        color2={"#CD3232"}
                      />
                    </div>
                  </div>
                ))}
            </div>
          </Container>
        </div>
      </Fragment>
    );
  }
}
function mapStateToProps(state) {
  return {
    houses: state.houses,
    reference: state.reference
  };
}

export default connect(mapStateToProps, { getHouses })(withRouter(Results));
