import axios from "axios";
import { url } from "./config";

const SIGN_IN_PATH = "/api/auth/token/login/";
const CURRENT_USER_PATH = "/api/auth/users/me";
const SIGN_UP_PATH = "/api/auth/register";
const LOG_OUT_PATH = "/api/auth/logout";

export function getUserData(token) {
  return axios({
    method: "GET",
    url: url + CURRENT_USER_PATH,
    headers: {
      "Content-Type": "application/json",
      Authorization: "Token " + token
    }
  });
}

export function signInWithEmailAndPassword(email, password) {
  return axios({
    method: "POST",
    url: url + SIGN_IN_PATH,
    data: {
      email: email,
      password: password
    }
  });
}

export function signUpWithEmailAndPassword(
  email,
  phone,
  gender,
  first_name,
  last_name,
  birth_day,
  password
) {
  return axios({
    method: "post",
    url: url + SIGN_UP_PATH,
    data: {
      email: email,
      phone: phone,
      gender: gender,
      first_name: first_name,
      last_name: last_name,
      birth_day: birth_day,
      password: password
    }
  });
}

export function signOut() {
  const token = localStorage.getItem("token");
  localStorage.removeItem("token");

  return axios({
    method: "DELETE",
    url: url + LOG_OUT_PATH,
    headers: {
      Authorization: "token " + token
    }
  });
}

export function updateUserData(data) {
  const token = localStorage.getItem("token");
  return axios({
    method: "put",
    url: url + CURRENT_USER_PATH,
    headers: {
      Authorization: "token " + token,
      "Content-Type": "application/x-www-form-urlencoded"
    },
    data
  });
}
