import React, { Component } from "react";
import { connect } from "react-redux";

import { withRouter } from "react-router-dom";

import "./Sidebar.scss";
class Sidebar extends Component {
  goToProfile = () => {
    this.props.history.push("/profile");
  };
  goToMyAds = () => {
    this.props.history.push("/myads");
  };
  goToSettings = () => {
    this.props.history.push("/settings");
  };
  goToSupport = () => {
    this.props.history.push("/support");
  };
  goToAbout = () => {
    this.props.history.push("/about");
  };
  // goToProfile = () => {
  //   this.props.history.push("/profile");
  // };

  render() {
    const { user } = this.props;
    const path = this.props.path.replace("/", "");
    return (
      <div className="Sidebar">
        <button
          onClick={this.goToProfile}
          className={`Sidebar__button ${
            path === "profile" ? "Sidebar__active" : ""
          }`}
        >
          Мой профиль
        </button>
        {user && user.user_type === 1 ? (
          <button
            onClick={this.goToMyAds}
            className={`Sidebar__button ${
              path === "myads" || path === "newad" ? "Sidebar__active" : ""
            }`}
          >
            Мои объявления
          </button>
        ) : null}
        <button
          onClick={this.goToSettings}
          className={`Sidebar__button ${
            path === "settings" ? "Sidebar__active" : ""
          }`}
        >
          Настройки
        </button>
        <button
          onClick={this.goToSupport}
          className={`Sidebar__button ${
            path === "support" ? "Sidebar__active" : ""
          }`}
        >
          Поддержка
        </button>
        <button
          onClick={this.goToAbout}
          className={`Sidebar__button ${
            path === "about" ? "Sidebar__active" : ""
          }`}
        >
          О нас
        </button>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    user: state.auth.user
  };
}

export default connect(mapStateToProps, null)(withRouter(Sidebar));
