class App extends Component {
  componentDidMount() {
    const token = localStorage.getItem("token");
    const { getCurrentUser, getCities } = this.props;
    if (token !== null) {
      getCurrentUser();
    }
    getCities();
  }
  render() {
    return (
      <Fragment>
        <Header />
        <div className="App">
          <Switch>
            <Route exact path="/" component={Main} />
            <Route exact path="/results" component={Results} />
            <Route exact path="/housing/:id" component={Housing} />
            <Route exact path="/apartments" component={Apartments} />
            <Route exact path="/saved" component={Saved} />
            <Route exact path="/reservations" component={Reservations} />

            <Route exact path="/profile" component={User} />
            <Route exact path="/myads" component={User} />
            <Route exact path="/newad" component={User} />
            <Route exact path="/settings" component={User} />
            <Route exact path="/support" component={User} />
            <Route exact path="/about" component={User} />
            {/* <PrivateRoute
            exact
            path="/saved"
            isAuthenticated={auth.isAuthenticated}
            component={Saved}
          /> */}
          </Switch>
        </div>
        <Footer />
      </Fragment>
    );
  }
}

function mapStateToProps(state) {
  return {
    auth: state.auth,
    user: state.auth.user
  };
}

export default connect(mapStateToProps, { getCurrentUser, getCities })(
  withRouter(App)
);
