export function getHouse(id) {
  return axios({
    method: "get",
    url: url + HOUSES_PATH + id
  });
}
export function createHouse(token) {
  return axios({
    method: "post",
    url: url + HOUSES_PATH,
    data: {
      rooms: 2,
      floor: 2,
      address: "984 1st Avenue",
      longitude: 76.854836,
      latitude: 43.205475,
      city: "Almaty",
      house_type: 2,
      price: 122,
      status: 1,
      photos: [],
      accoms: []
    },
    headers: {
      Authorization: "token " + token
    }
  });
}
export function deleteHouse(id) {
  return axios({
    method: "delete",
    url: url + HOUSES_PATH + id
  });
}
export function updateHouse(id) {
  return axios({
    method: "put",
    url: url + HOUSES_PATH + id,
    data: {
      floor: 35
    }
  });
}
