import React, { Component, Fragment } from "react";
import { connect } from "react-redux";
import {
  signIn,
  signUp,
  signOut,
  toggleSignInModal,
  toggleSignUpModal,
  toggleLoading,
  getCurrentUser
} from "../../actions/user.action";
import { Link } from "react-router-dom";
import InputMask from "react-input-mask";
import googleIcon from "../../assets/images/google-icon.png";
import logo from "../../assets/images/akv-logo-kz.png";
import Modal from "react-bootstrap/Modal";
import Collapse from "react-bootstrap/Collapse";
import Form from "react-bootstrap/Form";
import { FontAwesomeIcon as Fa } from "@fortawesome/react-fontawesome";
import { faAngleDown } from "@fortawesome/free-solid-svg-icons";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import "./Header.scss";

import { withRouter } from "react-router-dom";

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      signIn: {
        firstCredential: "admin@mail.com",
        password: "admin"
      },
      signUp: {
        email: "testuser@gmail.com",
        phone: "+77089397233",
        gender: 1,
        first_name: "Aidyn",
        last_name: "Jussupov",
        birth_day: "2000-01-30",
        password: "password11@",
        confirmPassword: ""
      },
      profileDropdown: false
    };
  }
  onSignInChangeHandler = e => {
    let { name, value } = e.target;
    this.setState({
      signIn: {
        ...this.state.signIn,
        [name]: value
      }
    });
  };
  signInHandler = e => {
    e.preventDefault();

    const { signIn, toggleLoading, getCurrentUser } = this.props;
    const { firstCredential, password } = this.state.signIn;
    toggleLoading();
    signIn(firstCredential, password);
    getCurrentUser();
  };
  onSignUpChangeHandler = e => {
    let { name, value } = e.target;

    switch (name) {
      case "confirmPassword":
        console.log("ASD");
        this.setState({
          signUp: {
            ...this.state.signUp,
            [name]: value,
            [name + "Error"]: value !== this.state.signUp.password
          }
        });
        break;
      case "gender":
        let genderValue = value === "Мужской" ? 1 : 2;
        this.setState({
          signUp: {
            ...this.state.signUp,
            [name]: genderValue
          }
        });
        break;
      default:
        this.setState({
          signUp: {
            ...this.state.signUp,
            [name]: value
          }
        });
        break;
    }
  };
  signUpHandler = e => {
    e.preventDefault();
    const { signUp, toggleLoading } = this.props;
    const {
      email,
      phone,
      gender,
      first_name,
      last_name,
      birth_day,
      password
    } = this.state.signUp;
    toggleLoading();

    signUp(email, phone, gender, first_name, last_name, birth_day, password);
  };
  signUpBlurHandler = e => {
    let name = e.target.name;
    let label = name + "Error";

    this.setState({
      signUp: {
        ...this.state.signUp,
        [label]:
          this.state.signUp[name] === "" ||
          (name === "phone" && this.state.signUp[name] === "+7(   )-   -  -  ")
      }
    });
  };
  logoutHandler = () => {
    const { signOut } = this.props;
    signOut();
  };
  profileDropdownChangeHandler = () => {
    this.setState({
      profileDropdown: !this.state.profileDropdown
    });
  };

  goToMain = () => {
    this.props.history.push("/");
  };
  goToSaved = () => {
    this.props.history.push("/saved");
  };
  goToReservations = () => {
    this.props.history.push("/reservations");
  };
  goToProfile = () => {
    this.props.history.push("/profile");
  };
  goToSettings = () => {
    this.props.history.push("/settings");
  };
  goToSupport = () => {
    this.props.history.push("/support");
  };
  goToAbout = () => {
    this.props.history.push("/about");
  };
  render() {
    const { auth, user } = this.props;
    return (
      <Fragment>
        <header className="Header">
          <button className="Header__logo">
            <img className="Header__logo" src={logo} alt="logo" />
          </button>
          <ul className="Header__navs">
            <li
              onClick={this.goToMain}
              className="Header__nav-item Header__nav-item--active"
            >
              Главная
            </li>
            {user ? (
              <Fragment>
                <li
                  onClick={this.goToSaved}
                  className="Header__nav-item Header__profile"
                >
                  Сохранённые
                </li>
                <li
                  onClick={this.goToReservations}
                  className="Header__nav-item Header__profile"
                >
                  Мои бронирования
                </li>
                <li className="Header__nav-item Header__profile">Сообщения</li>
                <li
                  className="Header__nav-item Header__profile"
                  onClick={() => this.profileDropdownChangeHandler()}
                  aria-controls="example-collapse-text"
                  aria-expanded={this.state.profileDropdown}
                >
                  Мой профиль {"\u00A0"} <Fa icon={faAngleDown} />
                  <Collapse in={this.state.profileDropdown}>
                    <div
                      className="Header__collapse"
                      id="example-collapse-text"
                    >
                      <div
                        onClick={this.goToProfile}
                        className="Header__collapse--item"
                      >
                        <img
                          className="Header__collapse--avatar"
                          src={user.userpic}
                          alt=""
                        />
                        <p className="Header__collapse--username">
                          {user.first_name}
                        </p>
                      </div>
                      <div className="Header__collapse--item">
                        <p className="Header__collapse--title">Режим хозяина</p>

                        <div
                          className={`toggle ${
                            user.user_type ? "toggle--active" : ""
                          }`}
                        ></div>
                        {/* toggle */}
                      </div>
                      <div
                        onClick={this.goToSettings}
                        className="Header__collapse--item"
                      >
                        <p className="Header__collapse--title">Настройки</p>
                      </div>
                      <div
                        onClick={this.goToSupport}
                        className="Header__collapse--item"
                      >
                        <p className="Header__collapse--title">Поддержка</p>
                      </div>
                      <div
                        onClick={this.goToAbout}
                        className="Header__collapse--item"
                      >
                        <p className="Header__collapse--title">О нас</p>
                      </div>
                    </div>
                  </Collapse>
                </li>

                <li className="Header__nav-item" onClick={this.logoutHandler}>
                  Выйти
                </li>
              </Fragment>
            ) : (
              <li
                className="Header__nav-item"
                onClick={() => this.props.toggleSignInModal()}
              >
                Войти
              </li>
            )}
          </ul>
        </header>

        <Modal
          size="lg"
          show={this.props.signInModal}
          onHide={() => this.props.toggleSignInModal()}
          centered={true}
        >
          <Modal.Body>
            <form onSubmit={this.signInHandler} className="Form__signIn">
              <h1 className="Form__title">Войдите в аккаунт</h1>
              <input
                value={this.state.signIn.firstCredential}
                name="firstCredential"
                onChange={this.onSignInChangeHandler}
                className="Form__input"
                type=""
                placeholder="Номер телефона или E-mail"
              />
              <Form.Group
                style={{
                  display: "flex",
                  justifyContent: "flex-end",
                  flexDirection: "column"
                }}
              >
                <input
                  name="password"
                  className="Form__input"
                  type="password"
                  value={this.state.signIn.password}
                  onChange={this.onSignInChangeHandler}
                  placeholder="Пароль"
                />
                <button className="Form__button--secondary Form__button--secondary-forgot">
                  Забыли пароль?
                </button>
                <h6 className="Form__error">{auth.authError}</h6>
              </Form.Group>
              <Link
                className="Form__button Form__button--login"
                to={"/settings"}
                onClick={this.signInHandler}
              >
                <button
                  className="Form__button Form__button--login"
                  type="submit"
                >
                  {this.props.auth.loading ? "Подождите...." : "Войти"}
                </button>
              </Link>

              <h6 className="Form__divider-or">или</h6>
              <button className="Form__button Form__button--google">
                Войти через Google{" "}
                <img
                  className="Form__button--icon"
                  src={googleIcon}
                  alt="sign in with google"
                />
              </button>

              <button
                style={{ marginTop: "15px", marginBottom: "60px" }}
                className="Form__button--secondary"
                type="submit"
                onClick={e => {
                  e.preventDefault();
                  this.props.toggleSignInModal();
                  this.props.toggleSignUpModal();
                }}
              >
                Создать аккаунт
              </button>
            </form>
          </Modal.Body>
        </Modal>
        <Modal
          size="xl"
          show={this.props.signUpModal}
          onHide={() => this.props.toggleSignUpModal()}
          centered={true}
        >
          <Modal.Body>
            <h1 className="Form__title">Создайте аккаунт</h1>
            <Row>
              <Col>
                <form className="Form__signUp">
                  <Form.Group>
                    <label
                      htmlFor="phoneInput"
                      className={`Form__input-label ${
                        this.state.signUp.phoneError === true
                          ? "Form__input-label--error"
                          : ""
                      }`}
                    >
                      Номер телефона
                    </label>
                    <InputMask
                      name="phone"
                      onBlur={this.signUpBlurHandler}
                      onChange={this.onSignUpChangeHandler}
                      value={this.state.signUp.phone}
                      mask="+7(999)-999-99-99"
                      maskChar=" "
                    >
                      {inputProps => (
                        <input
                          id="phoneInput"
                          className="Form__input"
                          {...inputProps}
                          type=""
                          placeholder="+7(   )"
                        />
                      )}
                    </InputMask>
                  </Form.Group>
                  <Form.Group>
                    <label
                      htmlFor="emailInput"
                      className={`Form__input-label ${
                        this.state.signUp.emailError === true
                          ? "Form__input-label--error"
                          : ""
                      }`}
                    >
                      Электроная почта
                    </label>
                    <input
                      id="emailInput"
                      name="email"
                      onBlur={this.signUpBlurHandler}
                      onChange={this.onSignUpChangeHandler}
                      value={this.state.signUp.email}
                      className="Form__input"
                      type="email"
                      placeholder="E-mail"
                    />
                  </Form.Group>
                  <Form.Group>
                    <label
                      className={`Form__input-label ${
                        this.state.signUp.passwordError === true
                          ? "Form__input-label--error"
                          : ""
                      }`}
                    >
                      Пароль
                    </label>
                    <input
                      name="password"
                      onBlur={this.signUpBlurHandler}
                      onChange={this.onSignUpChangeHandler}
                      value={this.state.signUp.password}
                      className="Form__input"
                      type="password"
                      placeholder="Придумайте пароль"
                    />
                    <Form.Group></Form.Group>
                    <label
                      className={`Form__input-label ${
                        this.state.signUp.confirmPasswordError === true
                          ? "Form__input-label--error"
                          : ""
                      }`}
                    >
                      Повторите пароль
                    </label>
                    <input
                      name="confirmPassword"
                      onBlur={this.signUpBlurHandler}
                      onChange={this.onSignUpChangeHandler}
                      value={this.state.signUp.confirmPassword}
                      className="Form__input"
                      type="password"
                      placeholder="Повторите свой пароль"
                    />
                  </Form.Group>

                  <h6
                    style={{ width: "300px", marginTop: "20px" }}
                    className=""
                  >
                    <button
                      className="Form__button--secondary"
                      type="submit"
                      onClick={e => {
                        e.preventDefault();
                        this.props.toggleSignInModal();
                        this.props.toggleSignUpModal();
                      }}
                    >
                      Войти в аккаунт
                    </button>
                  </h6>
                </form>
              </Col>
              <Col>
                <form className="Form__signUp">
                  <Form.Group>
                    <label
                      className={`Form__input-label ${
                        this.state.signUp.first_nameError === true
                          ? "Form__input-label--error"
                          : ""
                      }`}
                    >
                      Имя пользователя
                    </label>
                    <input
                      name="first_name"
                      onBlur={this.signUpBlurHandler}
                      onChange={this.onSignUpChangeHandler}
                      value={this.state.signUp.first_name}
                      className="Form__input"
                      type="text"
                      placeholder="Придумайте имя пользователя"
                    />
                  </Form.Group>
                  <Form.Group>
                    <label
                      className={`Form__input-label ${
                        this.state.signUp.birth_dayError === true
                          ? "Form__input-label--error"
                          : ""
                      }`}
                    >
                      День рождение
                    </label>
                    <input
                      name="birth_day"
                      onBlur={this.signUpBlurHandler}
                      onChange={this.onSignUpChangeHandler}
                      value={this.state.signUp.birth_day}
                      className="Form__input"
                      type="date"
                      placeholder="ДД/ММ/ГГГГ"
                    />
                  </Form.Group>
                  <Form.Group>
                    <label className="Form__input-label">Пол</label>
                    <select
                      name="gender"
                      onChange={this.onSignUpChangeHandler}
                      value={
                        this.state.signUp.gender === 1 ? "Мужской" : "Женский"
                      }
                      className="Form__input"
                      type="password"
                      placeholder="Выберите пол"
                    >
                      <option>Мужской</option>
                      <option>Женский</option>
                    </select>
                  </Form.Group>

                  <p className="Form__policy">
                    Нажимая кнопку Создать, вы
                    <br />
                    соглашаетесь c{" "}
                    <span className="Form__policy--main">
                      политикой конфеденциальности
                    </span>{" "}
                    и{" "}
                    <span className="Form__policy--main">
                      правилами
                      <br /> пользования.
                    </span>
                  </p>

                  <h6 className="Form__error">{auth.authError}</h6>
                  <button
                    style={{ marginTop: "20px" }}
                    onClick={this.signUpHandler}
                    className="Form__button"
                    type="submit"
                  >
                    {this.props.auth.loading ? "Подождите...." : "Создать"}
                  </button>
                </form>
              </Col>
            </Row>
          </Modal.Body>
        </Modal>
      </Fragment>
    );
  }
}

function mapStateToProps(state) {
  return {
    auth: state.auth,
    user: state.auth.user,
    signInModal: state.auth.signInModal,
    signUpModal: state.auth.signUpModal
  };
}

export default connect(mapStateToProps, {
  signIn,
  signUp,
  signOut,
  toggleSignInModal,
  toggleSignUpModal,
  toggleLoading,
  getCurrentUser
})(withRouter(Header));
