import React, { Component } from "react";
import "./Reservations.scss";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { getReservations } from "../../services/reservations";

class Reservations extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    console.log("asd");
    getReservations()
      .then(data => {
        this.setState({
          reservations: data.data.results
        });
      })
      .catch(e => {
        console.log(e);
      });
  }
  render() {
    const { reservations } = this.state;
    return (
      <Container>
        <div className="Reservations">
          <h2 className="Reservations__title">Мои бронирования</h2>

          {reservations && reservations.length > 0 ? (
            <Row>
              {reservations.map((result, index) => (
                <Col lg={6}>
                  <div className="Reservations__list--item" key={index}>
                    <img
                      className="Reservations__list--image"
                      src="https://p7.hiclipart.com/preview/336/946/494/avatar-user-medicine-surgery-patient-avatar.jpg"
                      alt=""
                    />
                    <p className="Reservations__list--title">
                      {result.house.name}
                    </p>
                    <p className="Reservations__list--status">status</p>
                    <p className="Reservations__list--response">response</p>
                    <p className="Reservations__list--description">asdasd</p>
                    <button className="Reservations__list--button">
                      Отменить
                    </button>
                  </div>
                </Col>
              ))}
            </Row>
          ) : (
            <div className="Reservations__empty">
              <p className="Reservations__paragraph">
                Бронируйте места в жильё и проводите
                <br /> свои лучшие время с нами.
              </p>
              <button className="Reservations__button">
                Забранировать жильё
              </button>
            </div>
          )}
        </div>
      </Container>
    );
  }
}

export default Reservations;
